export default {
	data() {
		return {
			windowHeight:'100%',
			fstime:'获取验证码',
			otime:60,
			butDis:false,
			sjvalue:1,
			yzmvalue:1,
			mimavalue:''
		};
	},
	onLoad() {
		var _me = this;
		
		uni.getSystemInfo({
			success: function(res) {
				_me.windowHeight = res.windowHeight + 'px';
			}
		});
		
	},
	methods:{
		onKeyInput:function(e){
			this.sjvalue = e.target.value;
		},
		yzmInput:function(e){
			this.yzmvalue = e.target.value;
		},
		mimaInput:function(e){
			this.mimavalue = e.target.value;
		},
		fsclick:function(){
			if(this.sjvalue.length !== 11){
				uni.showModal({
					title: '提示',
					content: '请输入正确手机号码！',
					showCancel: false
				});
			}else{
				var howe = setInterval(() => {
					if(this.otime == 0){
						this.fstime = "获取验证码";
						this.otime = 60;
						this.butDis = false;
						clearInterval(howe);
					}else{
						this.butDis = true;
						this.fstime = this.otime + 's后重新发送';
						this.otime --;
					}
				},1000);
			};
		},
		btnZhuce:function(){
			if(this.sjvalue.length !== 11){
				uni.showModal({
					title: '提示',
					content: '请输入正确手机号码！',
					showCancel: false
				});
			}else if(this.yzmvalue.length !== 6){
				uni.showModal({
					title: '提示',
					content: '验证码不正确！',
					showCancel: false
				});
			}else if(this.mimavalue == ''){
				uni.showModal({
					title: '提示',
					content: '请设置密码！',
					showCancel: false
				});
			}else if(this.mimavalue.length < '8'){
				uni.showModal({
					title: '提示',
					content: '请输入8-16位密码！',
					showCancel: false
				});
			}else{
				var me = this;
				uni.setStorage({
					key: 'howeKey',
					data: {
						shouji: me.sjvalue,
						mima: me.mimavalue
					},
					success: function () {
						uni.showModal({
							title: '恭喜',
							content: '注册成功！',
							showCancel: false,
							success: function (res) {
								if (res.confirm) {
									uni.redirectTo({
										url: 'login'
									});
								}
							}
						});
					},
					fail: function(){
						uni.showModal({
							title: '提示',
							content: '注册失败！',
							showCancel: false
						});
					}
				});
			}
		},
	}
}
